/* 全局样式 */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url('/public/images/background.jpg');
  background-size: cover; /* 让图片填充整个背景 */
  background-repeat: no-repeat; /* 防止图片重复 */
  background-position: center; /* 图片居中显示 */
  background-attachment: fixed; /* 背景图片固定，不随页面滚动 */
}

/* 标题、段落等默认样式 */
h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

/* 通用容器样式 */
.container {
  max-width: 360px; /* 缩小宽度 */
  margin: 20px;
  padding: 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* 标题样式 */
.container h1 {
  font-size: 1.8em;
  color: #007bff;
  margin-bottom: 20px;
}

/* 表单输入框样式 */
form input {
  width: 90%;
  padding: 20px;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
  background-color: #f9f9f9;
}

form input:focus {
  outline: none;
  border-color: #007bff;
  background-color: #ffffff;
}

/* 按钮样式 */
form button {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #0056b3;
}

form button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* 主页面样式 */
.home-container {
  text-align: center;
  padding: 100px 80px;
  background-color: #ffffff;
  max-width: 500px;
  margin: auto;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.home-header h1 {
  font-size: 2em;
  color: #007bff;
  margin-bottom: 10px;
}

.home-header span {
  color: #333;
  font-weight: bold;
}

.home-header p {
  font-size: 1.2em;
  color: #555;
  margin-bottom: 20px;
}

.home-actions {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.home-button {
  padding: 10px 20px;
  font-size: 1em;
  color: white;
  background-color: #007bff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.home-button:hover {
  background-color: #0056b3;
}

/* 上传页面样式 */
.upload-container {
  max-width: 360px; /* 缩小宽度 */
  margin: 20px auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.upload-container h1 {
  font-size: 1.8em;
  margin-bottom: 15px;
}

.upload-container form {
  margin-top: 20px;
}

.upload-container input[type="file"] {
  margin-bottom: 15px;
  padding: 8px;
}

.upload-container button {
  width: 100%;
  padding: 10px;
  font-size: 1em;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.upload-container button:hover {
  background-color: #0056b3;
}

.success-message {
  margin-top: 20px;
  background: #d4edda;
  color: #155724;
  padding: 10px;
  border: 1px solid #c3e6cb;
  border-radius: 4px;
  text-align: center;
  font-size: 0.9em;
}

.file-info {
  margin-top: 20px;
  text-align: left;
  background: #f9f9f9;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.9em;
  color: #555;
}

.file-info p {
  margin: 5px 0;
}

.job-container {
  text-align: center;
  padding: 20px;
}

.job-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.job-card {
  background: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 250px;
  text-align: left;
}

.job-card h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #007bff;
}

.job-card p {
  font-size: 1em;
  color: #555;
}

.job-card button {
  margin-top: 10px;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.job-card button:hover {
  background-color: #0056b3;
}

.upload-section {
  margin-top: 30px;
}

.upload-button {
  padding: 15px 30px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 1.2em;
  cursor: pointer;
}

.upload-button:hover {
  background-color: #0056b3;
}

.job-details-container {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.requirements-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.requirements-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.requirements-table td:first-child {
  font-weight: bold;
}

.upload-section {
  margin-top: 20px;
}

.upload-button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.upload-button:hover {
  background-color: #0056b3;
}
.job-container, .admin-dashboard, .job-details-container {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.files-table, .requirements-table {
  width: 100%;
  border-collapse: collapse;
}

.files-table th, .requirements-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}

.admin-dashboard {
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.files-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.files-table th, .files-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.files-table th {
  background-color: #007bff;
  color: white;
}
